'use server';
import { getKindeServerSession } from "@kinde-oss/kinde-auth-nextjs/server";
import { syncUser } from "./lib/syncUser";
import Home from "./page";



export default async function HomeClient() {
  const { getUser, isAuthenticated } = getKindeServerSession();
  const user = await getUser();
  await syncUser(); // Sync user with our DB to make sure we have their latest info (e.g., last login time)
  const authenticated = await isAuthenticated();


const plainUser = JSON.parse(JSON.stringify(user)); // to remove Mongoose document methods
// needed because PostClient is a Client Component and cannot receive Mongoose document directly
// (could also use user.toObject() if it were available)

  const homeElement = await Home({ user: plainUser }, { authenticated: authenticated ?? false }); // pass user and authenticated status as props
  return homeElement;
}
